<template>
  <div id="dashboard">
    <h3>Dashboard</h3>
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div><u>{{employee.employee_id}}</u>: {{employee.name}}
        <router-link class="secondary-content" :to="{ name: 'view-employee', params: {employee_id: employee.employee_id} }" >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large blue">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit'

  export default {
    name : 'dashboard',
    data () {
      return {
        employees : []
      }
    },
    created() {
      db.collection('employees').orderBy('dept').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          //console.log(doc.data());
          const data = {
            'id' : doc.id,
            'employee_id' : doc.data().employee_id,
            'name' : doc.data().name,
            'dept' : doc.data().dept,
            'position' : doc.data().position
          }
          this.employees.push(data)
        })
      })
    }
  }
</script>



